.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  margin: 0;
}
.app-container {
  position: absolute;
  margin: 0;
  height: 100vh;
  width: 100vw;
}

.carousel-container {
  position: relative;
  width: 100vw;
  display: flex;
  justify-content: center;
  overflow-x: auto;
}

.img-card {
  border-radius: 20px !important;
  min-height: 400px;
  margin-top: 10px;
  margin-bottom: 10px;
}
